import { Component, Input, Output, EventEmitter, ViewChildren, forwardRef, QueryList } from '@angular/core';
import { PolicyInfo } from './security/policy';

export abstract class BaseComponent {
    appliedPolicies: PolicyInfo[]=[];
    //
    @Input() name: string;
    @Input() allowPolicies: boolean = true;
    @ViewChildren(BaseComponent) children: QueryList<BaseComponent>;

    abstract applyPolicy(policy: PolicyInfo): void; 

    isPolicyApplied(policyName: string): boolean {
        let applied: boolean = false;
        this.appliedPolicies.forEach(policy => {
            if (policy.nombre == policyName) {
                applied = true;
            }
        });
        return applied;
    }

    applyPolicies(policies: PolicyInfo[]): void {

    }
    
}